@media (max-width: 800px){


   .cards{
       width: 90%;
   }

   .header_side {
    display: none !important;
    
}

.logo_container {
    display: none !important;
}


.ul li a {
    font-size: 12px !important;
}

#carrito {
    width: 25%;
  
}





.main_nav_item {
    display: inline-block;
    margin-right: 30px;
}

.header_content {
    display: block !important;
    margin: 0px !important;
    padding: 0px !important;
    width: 80%;
    height: 100%;
}

.header{
    width:90%
}

.hamburger_container{

    display:none;

}


.navbar {
    display: flex;
    flex-flow: row wrap;
    height: 120px;
    font-size: 15%;
    flex-grow: 1;
    justify-content: space-around;
}

aside {
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    width: 95%;
    margin-top: 50px;
    margin-right: 1%;
}

iframe{
    width: 100%;
    height:600px;
}

iframe .Embed a {
    width: 100%;
    height: 45%;
    height: #262626;
    color: rgba(var(--i1d,38,38,38),1);
    /* height: 522px; */
}

#footer div:nth-child(5) p{


    margin-top: 20px;
}


.form-usuario {

    margin: 2%;
    margin-left: 1%;
}

}



@media (max-width: 630px) { 




    .datosUsersOcultar{

        display:none !important;
    }

 
    .content-home {
     
        flex-flow: column wrap;
       
    }


    .content-cards {
        width: 90%;
   
    }

    .img-product {

        height: 180px;
  
    }

    .header{
        background: black !important;
        width:90%;
        height: 106px;
        left:49%;
        color: white;
    }

    .hero_slide_content h1 span {
 
        font-size: 20px;
    }
    
    #menu-movil{
        display: flex;
        flex-flow: row wrap;
    }

    #menu-movil ul{
        top: 106px !important;
    }


    #entrar{
 
            display: block;
            width: 55%;
            height: auto;
            margin-left: 0px;

    }

    #entrar button{

        display: block;
        width: 70%;
        height: 103px;
        margin-left: 48px;
        margin-top:1px;
      
        padding: 5px;
        background: black;
        color: white;
        border-radius: 5px;
    }



    img, svg {
        display:block;
        width: 110px;
        height: 106px;
        padding: 5px;
    }
    .navbar {

        display: none;
        /* display: flex;
        flex-flow: column wrap;
        height: 260px;
        font-size: 16%;
        flex-grow: 1;
        justify-content: space-around; */
    }


    dl > ol > ul {
        display: flex;
        flex-flow: column wrap;
    }

    .ul li a {
        display:block;
        border-radius: 5px;
        font-size: 15px !important;
        padding: 5px;
    }

    .ul li a:hover {
        background: black;
        color: white;
    }

    .main_nav {
        width: 100%;
    }
    
    
element.style {
    background: white;
    position: absolute;
    top: 85px;
    left: -106px;
}

#footer div {

    font-size: 10px;
}


#footer div:nth-child(5) p{


    margin-top: 40px;
}




input[type="text"],input[type="email"],input[type="password"],textarea{


    width:90% !important;

}

input[type="file"]{

    font-size: 10px !important;
}

form{
    width: 98% !important;
}



tbody, td, tfoot, th, thead, tr {
    
    font-size: 10px !important;
}


#footer div:nth-child(5) p {

     font-size: 9px !important; 
 
 }

 .cargando {
   
    left: 25%;
    top: 35%;

}


.hero_slide_content > div{

    display: flex;
    flex-flow: row wrap;

}


.hero_slide_content{

    margin-top:0px;

}


}



 @media (max-width: 500px){


  
    .logoheader img {
        width: 104%;
    }

    #entrar button {
   
        margin-left: 35px;
    
    }

    input[type="submit"], input[type="button"] {

        width: 60%;
   
    }


    #swal2-input {
        margin-left: 5% !important;
    }


